"use strict";
boann.controller('MyaccountController', ['$scope', '$http', '$window', function($scope, $http, $window) {

    var statePage       = $window.location.pathname.split("/")[2].split(".")[0];
    var searchPage      = $window.location.search.split("=")[1];
    var URI             = controler.view + "account/index.php";

    //console.log(statePage);

    switch(statePage){
        case "editUser" :
            getThisUser(searchPage);

            $scope.updateUser = function(data){
                if(data){
                    var VALUES = [{data:true, data:data, uuid:searchPage}];
                    $http.post(URI, VALUES, {params:{action:'updateUser'}}).then(function(data){
                        console.log(data.data);
                        switch(data.data.data){
                            case "success" :
                                location.href = data.data.dataURL;
                            break;

                            case "error":
                                swal("Oeps!", data.data.dataContent, "error");
                            break;
                        }
                    });
                }
            }
        break;

        case "my-account" :
            $scope.savePassword = function(data){
                console.log(data);
            }

            me();
        break;
    }

    function me(){
        $http.get(URI, {params:{action:"me"}}).then(function(data){
            if(data.status === 200){
                $scope.me = data.data;
            }
        });
    }

    function getThisUser(uuid){
        $http.get(URI, {params:{action:"getThisUser", uuid:uuid}}).then(function(data){
            if(data.status === 200){
                $scope.user = data.data;
            };
        });
    }

}]);
